<template lang="">
  <div class="w-full top-0 bg-transparent px-[2%] py-2.5">
    <div class="max-w-7xl h-[120px] mx-auto flex justify-between items-center">
      <div class="logo">
        <img src="https://via.placeholder.com/120x50" alt="logo">
      </div>
      <ul class="flex basis-80 justify-between items-center">
        <ul @mouseover="isHover = true" @mouseout="isHover = false" class="flex basis-52 justify-center items-center cursor-pointer">
          <li @click="$emit('lang', 0); SelectLang(0); isKorean = true; isEnglish = false" class="w-24 text-center text-lg text-white opacity-40 cursor-pointer hover:opacity-100" :class="isKorean === true && '!opacity-100 text-pink-600'">ko</li>
          <li class="basis-14 h-0.5 bg-white opacity-30 cursor-pointer hover:opacity-100" :class="isHover === true && '!opacity-100'"></li>
          <li @click="$emit('lang', 1); SelectLang(1); isEnglish = true; isKorean = false" class="w-24 text-center text-white text-lg opacity-40 cursor-pointer hover:opacity-100" :class="isEnglish === true && '!opacity-100 text-pink-600'">en</li>
        </ul>
        <ul class="basis-16 h-16 flex flex-wrap items-center gap-y-4 ml-6 py-5 cursor-pointer" @click="isOpen = !isOpen">
          <li class="basis-full h-0.5 bg-white transition-all duration-500" :class="isOpen === true && 'rotate-45 translate-y-2.5'"></li>
          <li class="basis-full h-0.5 bg-white transition-all duration-500" :class="isOpen === true && '-rotate-45 -translate-y-2.5'"></li>
        </ul>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'NavPage',
  props:{
    langList: Object,
  },
  data() {
    return {
      isOpen: false,
      isKorean: true,
      isEnglish: false,
      isHover: false,
    }
  },
  methods:{
    SelectLang(index){
      localStorage.setItem("language", index);
    }
  },
}
</script>
