<template>
  <!-- <NavMenu @dark="DarkMode()" /> -->

  <!-- 다크모드는 두가지 버전으로 동작할 수 있다.
  1. 첫번째 방법은 Nav 페이지에서 다크모드할 아이콘을 넣고 App.vue페이지의 template 태그 안에 <NavMenu @dark="DarkMode()" />를 쓴다. 그리고 methods에 다크모드 함수를 만들어 주고 classList에 dark를 toggleclass 해주면 다크모드가 된다. 이건 tailwindcss를 깔았을때만 되는데 왜냐하면 tailwindcss에는 다크모드가 세팅되어 저장되어 있어서 class="dark"를 해주면 자동으로 다크모드가 만들어진다.
  2. 두번째 방법은 터미널에 yarn add @vueuse/core를 깔아서 설치하는 방법인데 @vueuse/core에도 다크모드가 자동으로 설치되어 있다. 따라서 설치한 다음 App.vue 에서 임포트 해준다. import {useDark, useToggle} from '@vueuse/core'; 이렇게 임포트 해주는데 이때 이름을 바꾸면 안된다.useDark 와 useToggle는 @vueuse/core에 저장되어 있는 이름이므로 {useDark, useToggle}를 이름 바꾸지 않고 그대로 써준다. 그런 다음 데이터에 useDark 와 useToggle 함수를 변수로 받아준다. 그런 다음 Nav.vue에서 dark 이벤트를 props 해와서 App.vue에서 다크모드 이벤트에 다크모드를 일으키는 toggleDark()를 써주면 끝이다. -->
  <!-- <FontBox :langList="langList[lang]"/> -->
  <!-- <NavMenu @MenuIndex="MenuIndex = $event;" @click="SectionMove()" @dark="toggleDark()" :isDark = "isDark" /> -->

  <!-- 위의 둘 중에 하나만 선택해서 사용하면 된다. 하지만 위의 것을 선택하면 밑에 methods 부분에 함수를 바꿔야한다. -->

  <NavMenu @MenuIndex="SectionMove($event)" @dark="toggleDark()" :isDark = "isDark" @lang="lang=$event" :langList="langList[lang]" class="fixed bg-zinc-800 z-50" /> <!--원하는 이벤트 내용 lang=$event라고 쓴다. 즉 @뒤에는 이벤트 이름을 넣고 받을 값은 우리가 지어준 변수 이름(여기서는 lang)=$event 라고 쓴다.-->
  {{ MenuIndex }}
  <div ref="section-0">
    <Home :langList="langList[lang]" />
  </div>
  <div ref="section-1">
    <Portfolio :langList="langList[lang]" />
  </div>
  <div ref="section-2">
    <Profile :langList="langList[lang]" />
  </div>
  <div ref="section-3">
    <skill :langList="langList[lang]" />
  </div>
  <Footer />
</template>

<script>
import Home from './pages/Home.vue'
import Profile from './pages/Profile.vue'
import Skill from './pages/Skill.vue'
import Portfolio from './pages/Portfolio.vue'
import Footer from './components/Footer.vue'
import NavMenu from './components/Nav.vue';
// import FontBox from './components/FontBox.vue'
import {useDark, useToggle} from '@vueuse/core';
import Language from './assets/Language.json'
export default {
  name: 'App',
  components: {
    NavMenu,
    Footer,
    // FontBox,
    Home,
    Profile,
    Skill,
    Portfolio,
  },
  data() {
    return {
      lang: 0,
      isDark : useDark(),
      langList: Language,
      toggleDark : useToggle(useDark()),
      FontStyle: "",
      MenuIndex: 'section-1',
      ArrayList: '',
    }
  },
  methods:{
    DarkMode(){
      document.querySelector("html").classList.toggle("dark");
    },
    // SectionMove(){
    //   const rect = this.ArrayList[this.MenuIndex].getBoundingClientRect();
    //   // console.log(rect);
    //   const offset = rect.top + window.scrollY;
    //   window.scrollTo({
    //     top: offset,
    //     behavior: 'smooth',
    //   })
    // },

    // 위에 둘중에 아무거나 써도 된다. 위에 이벤트를 둘 중에 위의 것을 사용하면 위의 SectionMove를 사용하고 아래것을 사용할려면 밑의 SectionMove를 사용하면 된다.

    SectionMove(index){
      this.MenuIndex = index
      const rect = this.ArrayList[this.MenuIndex].getBoundingClientRect();
      // console.log(rect);
      const offset = rect.top + window.scrollY - 60;
      window.scrollTo({
        top: offset,
        behavior: 'smooth',
      })
      // scrollTo - 자바스크립트 기본 메소드 이며, 페이지의 특정 위치로 스크롤이 가능
      // window.scrollTo(x좌표, y좌표, 행동)
      // behavior - 스크롤 동작을 지정 "auto" 기본값 "smooth" 부드러운 애니메니션과 함께 스크롤
    },
  },
  mounted(){
    this.FontStyle = localStorage.getItem("font"); // 렌더링 되기 전에 원래 로컬 스토리지에 저장되어있는 font 값을 가져와서 내가 원래 선택했던 폰트가 새로고침이 되어도 제일 상단 폰트인 에스코어드림으로 가지 않고 그래로 유지될 수 있도록 해준다.
    this.lang = localStorage.getItem("language"); // 렌더링 되기 전에 원래 로컬 스토리지에 저장되어있는 language 값을 가져와서 내가 원래 선택했던 언어(한국어 or 영어)가 새로고침이 되어도 다시 한국어로 돌아가지 않고 그대로 유지될 수 있도록 해준다.
    document.querySelector("body").classList.add(this.FontStyle);
    this.ArrayList = this.$refs;
    // console.log(this.$refs);
  }
}
</script>

